.card {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20rem;
  margin: 1rem 0.5rem;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 3px 3px 13px 0px rgba(201,201,201,1);
}

.card-img {
  width: 5rem;
}

.card-title {
  color: #fff;
  font-weight: bold;
  font-size: 2rem;
  margin-left: 1rem;
}
